<template>
  <div class="Not-found">
    <el-result sub-title="Sorry, request error">
      <template #icon>
        <svg class="icon icon-not-found" aria-hidden="true">
          <use xlink:href="#icon-not-found"></use>
        </svg>
      </template>
      <template #extra>
        <el-button type="primary" @click="handleRefresh">刷新</el-button>
        <el-button type="primary" @click="handleBack">返回</el-button>
      </template>
    </el-result>
  </div>
</template>

<script lang="ts" setup>
import { LocalStorage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";

defineOptions({
  name: "notFound",
});

const { goPage, backPage, routeQuery } = useVueRouter();
const redirectPath = routeQuery.value.redirect;
const checkStatus = LocalStorage.getItem("bd_checkStatus");
const handleRefresh = () => {
  if (checkStatus === 2) {
    goPage(redirectPath ? redirectPath : "/home");
  } else {
    goPage(redirectPath ? redirectPath : "/certificationService");
  }
};
const handleBack = () => {
  backPage(-1);
};
</script>
<style lang="scss" scoped>
.Not-found {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}
.icon-not-found {
  width: 150px;
  height: 150px;
}
</style>
